const Product = ({id,title,image,price,rating}) => {
    return (
       <div className="dog">
           <div className="dog__info">
               <p>{title}</p>
           </div>
           <div className="dog__price">
               <small>$</small>
               <strong>{price}</strong>
           </div>
           <div className="dog__rating">
               {
                   Array(rating)
                   .fill()
                   .map((_) => (
                        <p>*</p>
                   ))
               }
           </div>
           <img src={image} alt="dog"></img>
           <button>Add to Cart</button>
       </div>
    );
}
 
export default Product;