import { Link } from 'react-router-dom';

const Navbar = () => {

    return (
        <div className="navbar-container">
            <div className="navbar">
                <h1 className="title">SLBARK.LK</h1>
                <div className="links">                    
                        <Link className="home" to="Home.js">HOME</Link>
                        <Link className="contact" to="/create">CONTACT</Link>
                        <Link className="profile" to="Profile.js">PROFILE</Link>
                        <Link className="about" to="/create">ABOUT</Link>
                </div>           
                <Link className="add-to-cart" to="add-cart.js">
                    <img src="../images/addcart.jpg" alt="purchase"></img>
                    <div className="basket-count">
                        <span className="basket-count-span">2</span>
                    </div>
                </Link>
            </div>
            
        </div>
        
    );
 }
 export default Navbar;